@import url('https://fonts.googleapis.com/css2?family=Cabin:ital,wght@0,400;1,500&display=swap');
* {
    box-sizing: border-box;
}
img{
    height: 100%;
}

 html {
        box-sizing: border-box;
       
       
        
    }
    *, *:before, *:after {
        box-sizing: border-box;
    }

body {
font-family: 'Cabin', sans-serif;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App {
    padding: 0 10%;
}
header {
    position: fixed;
    
    top: 0;
    right: 0;
    left: 0;
    height: 80px;
    display: flex;
    justify-content: space-between;
    padding: 1em;
    align-items: center;
    
}
header .logo img {
    height: 50px ;
}

h1 {
    font-size: 4em;
}
aside {
    position: fixed;
    top: 0;
    left: 0;
    
    height: 100vh;
}
nav.main-nav {
   
   
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: space-around;
    flex-direction: column;
    
}
nav.main-nav a {
    padding: .4em;
   appearance: none;
   font-style: italic;
   color: gray;
   text-decoration: none;
    transition: .5s;
    font-size: 1.2em;
transform: rotate(-90deg);
   
}
nav.main-nav a:hover {
color:black;
}
.App-link {
  color: #61dafb;
}

body {
  background: url("/images/bg.svg") no-repeat center fixed ;
}
section {
    height:100vh;
    position: sticky;
}

section#about {
    display: flex;
    justify-content: left;
    align-items: center;
}